.about {
  width: 100%;
  position: relative;

  &__list {
    list-style: none;
    margin: 0;
  }

  &__item {
    display: flex;

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__item--icon {
    flex-shrink: 0;
    display: block;
    width: 48px;
    height: 48px;
    background: url("data:image/svg+xml,%3Csvg%20width%3D%2250%22%20height%3D%2250%22%20viewBox%3D%220%200%2050%2050%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Cg%20id%3D%22cfc2e2790c98f79a0cec94c1a3edffbd%22%3E%0A%20%20%20%20%3Cg%20id%3D%2270c0a256ece5cdc138bc7985db5e1175%22%3E%0A%20%20%20%20%20%20%3Cpath%20id%3D%22829069dc6032a29c983b445c2c787fed%22%20d%3D%22M15.394%2026.7418L20.137%2030.3618L27.753%2021.6578L26.247%2020.3418L19.863%2027.6378L16.606%2025.1518L15.394%2026.7418ZM34.753%2021.6578L33.247%2020.3418L26.878%2027.6208L26.125%2027.0188L24.875%2028.5808L27.122%2030.3788L34.753%2021.6578Z%22%20fill%3D%22%2300BA35%22%3E%3C%2Fpath%3E%0A%20%20%20%20%3C%2Fg%3E%0A%20%20%20%20%3Crect%20x%3D%220.5%22%20y%3D%220.5%22%20width%3D%2249%22%20height%3D%2249%22%20rx%3D%2224.5%22%20stroke%3D%22black%22%20stroke-opacity%3D%220.14%22%3E%3C%2Frect%3E%0A%20%20%3C%2Fg%3E%0A%3C%2Fsvg%3E");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin-right: 16px;
  }

  &__item--title {
    font-size: 24px;
    font-weight: 400;
    line-height: 1.2;
  }
}

@media (max-width: 600px) {
  .about__item--icon {
    width: 32px;
    height: 32px;
  }

  .about__item--title {
    font-size: 20px;
  }
}

@media (max-width: 480px) {
  .about__item {
    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }
  .about__item--icon {
    display: none;
  }

  .about__item--title {
    border-left: 3px solid var(--color-primary);
    padding-left: 16px;
  }
}

@media (max-width: 390px) {
  .about__item--title {
    font-size: 16px;
    font-weight: 500;
  }
  .about__item--text p {
    font-size: 14px;
  }
}
